import React from 'react';
import Particles from 'react-particles-js';
import ParticlesConfig from '../../components/Particles'
import Typewriter from 'typewriter-effect';
import './Home.css'
import { Button } from 'react-router-dom'

class Home extends React.Component {

    render() {
        return(
            <div className="container m-0">
                {/* Home */}
                <section id="home">
                    <div className="home is-flex has-text-white is-justify-content-center is-align-content-center is-align-items-center">
                        <div className="columns is-multiline">
                            <div className="column is-full">
                                <div className="logo is-flex is-full is-justify-content-center is-align-content-center is-align-items-center">
                                    <img src="/logo.svg" alt="logo"></img>
                                </div>
                                <div className="mt-5 columns is-mobile">
                                    <div className="column is-flex is-justify-content-center is-align-content-center is-align-items-center">
                                        <Typewriter
                                            onInit={(typewriter) => {
                                                typewriter.typeString('Welcome stranger!')
                                                .pauseFor(1000)
                                                .deleteAll()
                                                .typeString('Check out the buttons below!')
                                                .pauseFor(1000)
                                                .deleteAll()
                                                .typeString('&nbsp;<i class="fas fa-arrow-down"></i> <i class="fas fa-arrow-down"></i> <i class="fas fa-arrow-down"></i>')
                                                .callFunction(() => {
                                                    typewriter.stop();
                                                })
                                                .start();
                                            }}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="column is-full mt-5">
                                <div class="columns is-flex is-multiline is-flex is-multiline is-flex is-full is-justify-content-center is-align-content-center is-align-items-center">
                                    <div class="column column-button">
                                        <button className="outline has-text-white" onClick={()=>window.location.href='https://github.com/fede1132'}><i class="fab fa-github"></i> GitHub</button>
                                    </div>
                                    <div class="column column-button">
                                        <button to="/projects" className="outline has-text-white"><i class="fas fa-code"></i> Projcets</button>
                                    </div>
                                    <div class="column column-button">
                                        <button className="outline has-text-white" onClick={()=>window.location.href='https://www.spigotmc.org/members/fede1132.118978/'}><i class="fas fa-gamepad"></i> SpigotMC</button>
                                    </div>
                                    <div class="column column-button">
                                        <button className="outline has-text-white" onClick={()=>window.location.href='https://www.mc-market.org/members/62378/'}><i class="fas fa-gamepad"></i> MC-Market</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <Particles className="particles" params={ParticlesConfig} />
                        {/* <div className="scroll absolute bottom is-flex is-multiline is-flex is-multiline is-flex is-full is-justify-content-center is-align-content-center is-align-items-center">
                            <div class="columns is-flex is-multiline">
                                <div className="column is-size-5 is-full is-flex is-multiline is-flex is-multiline is-flex is-full is-justify-content-center is-align-content-center is-align-items-center">
                                    Scroll down
                                </div>
                                <div className="column is-full is-flex is-multiline is-flex is-multiline is-flex is-full is-justify-content-center is-align-content-center is-align-items-center">
                                    <div class="circle is-flex is-multiline is-flex is-multiline is-flex is-full is-justify-content-center is-align-content-center is-align-items-center">
                                        <i className="fas fa-arrow-down"></i>
                                    </div>
                                </div>
                            </div>
                        </div> */}
                    </div>
                </section>
            </div>
        );
    }

}

export default Home